@import "../../../global.scss";

.navigation {
  width: 100%;

  > .wrapper {
    width: 100%;
    display: flex;
    position: fixed;
    left: 0;
    top: 0;
    color: $nav-color;
    padding: 0 2rem;
    line-height: 5rem;
    z-index: 1000;
    transition: all 0.5s ease;
    justify-content: space-between;
    background-color: red;

    .left {
      display: flex;
      align-items: center;
      justify-content: center;
      .logo {
        span {
          font-size: xx-large;
        }
      }
      > .info {
        display: flex;
        align-items: center;
        justify-content: center;

        > a {
          display: flex;
          align-items: center;
          justify-content: center;
          text-decoration: none;
          color: $nav-color;
          margin-left: 3rem;
        }
      }
    }

    .right {
      display: flex;
      justify-content: center;
      align-items: center;

      > .hamcon {
        flex: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        > .hamburger {
          color: white;
          line-height: 3rem;
        }

        .hamburger {
          width: 4rem;
          height: 50%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          cursor: pointer;
          line-height: 5rem;
          text-align: center;

          span {
            width: 100%;
            height: 10px;
            margin: 6px 0;
            background-color: $nav-color;
            transition: all 0.4s ease-out;
            border-radius: 5px;
            transform-origin: left;
          }
        }
      }
    }

    > .menu {
      position: fixed;
      z-index: -1;
      right: -100vw;
      width: 50vw;
      background-color: #fff;
      color: white;
      height: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      transition: all 0.5s ease;
      box-shadow: 1px 1px 10px 2px #222;

      ul {
        list-style-type: none;
        display: flex;
        flex-direction: column;
        width: 100%;

        li {
          a {
            color: #bbb;
            text-decoration: none;
            display: block;
            text-align: center;
            font-size: large;

            &:hover {
              color: #fff;
              font-size: x-large;
            }
          }
        }
      }
    }

    &.active {
      background-color: $nav-color;
      color: $nav-text;

      .hamburger {
        span {
          background-color: #fff;
          &:first-child {
            background-color: white;
            transform: rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            background-color: white;
            transform: rotate(-45deg);
          }
        }
      }

      > .menu {
        right: 0;
        background-color: $nav-color;
      }
    }
  }
}
