@import "../../../global.scss";

.portfolio {
  > .wrapper {
    h1 {
      text-align: center;
      padding: 20px;
    }

    .link-list {
      ul {
        list-style-type: none;
        color: $nav-color;
        display: flex;
        align-items: center;
        justify-content: space-around;
        border: solid 3px $nav-color;
        border-radius: 10px;
        padding: 0;

        > .portfolioList {
          cursor: pointer;
          flex: 0.25;
          text-align: center;
          transition: ease 1s;
          padding: 10px;

          &.active {
            background-color: $nav-color;
            color: #fff;
            border-radius: 10px;
          }
        }
      }
    }

    > .items {
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;

      > .containimg {
        padding: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        width:100%;

        > .each-item {
          width: 33%;
          padding: 10px;
          border-radius: 10px;
          overflow: hidden;

          > img {
            max-width: 100%;
            width: 100%;
          }

          .fade {
            -webkit-animation-name: fade;
            -webkit-animation-duration: 1.5s;
            animation-name: fade;
            animation-duration: 1.5s;
          }
          @-webkit-keyframes fade {
            from {
              opacity: 0.4;
            }
            to {
              opacity: 1;
            }
          }

          @keyframes fade {
            from {
              opacity: 0.4;
            }
            to {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: $breakpoint) {
  .portfolio {
    > .wrapper {
      .items {
        .containimg {
          width: 100%;
        }
      }
    }
  }
}



@media screen and (min-width: $breakpoint) {
  .portfolio {
    > .wrapper {
      
      .items {
        width: 100%;
        .containimg {
          width:80%;
        }
      }
    }
  }
}


@media screen and (min-width: 900px) {
  .portfolio {
    > .wrapper {
      
      .items {
        width: 100%;
        .containimg {
          width:50%;
        }
      }
    }
  }
}

@media screen and (min-width: 1300px) {
  .portfolio {
    > .wrapper {
      
      .items {
        width: 100%;
        .containimg {
          width:40%;
        }
      }
    }
  }
}